import React, { Component } from 'react';
import './HallModel.css';
import { ListView } from 'antd-mobile';
import { Link } from 'react-router-dom';
import { Museum } from '../api'

const pathPrefix = window._config.pathPrefix;


class HallModel extends Component {

  constructor(props) {
    super(props);
    const dataSource_JBCL = new ListView.DataSource({
      rowHasChanged: (row1, row2) => row1 !== row2,
    });
    const dataSource_CSZL = new ListView.DataSource({
      rowHasChanged: (row1, row2) => row1 !== row2,
    });

  this.state = {
      items: [],
      items_JBCL : [],
      items_CSZL : [],
      dataSource_JBCL,
      dataSource_CSZL,
      isLoading: true,

    };
  }

  //耗时操作放在这里面
  async componentDidMount(){
    try{
      let data = await Museum.exhibitionList({
        type : "p",
      });
      if (data && data.code===true) {
        let items_JBCL = [];
        let items_CSZL = [];
        if (data.data.length > 0) {

          for(let i = 0; i < data.data.length; i++) {
            let item = data.data[i];
            if (item.pType === 0) {
                items_JBCL.push(item);
            }else {
                items_CSZL.push(item);
            }
          }
        }

        this.setState({
          items_JBCL :items_JBCL,
          items_CSZL :items_CSZL,
          dataSource_JBCL: this.state.dataSource_JBCL.cloneWithRows(items_JBCL),
          dataSource_CSZL: this.state.dataSource_CSZL.cloneWithRows(items_CSZL),
          items: data.data,
          isLoading: false,
        })
      }
    }catch(e){
      //
    }
  }

  render() {
    let index_JBCL = 0;
    let index_CSZL = 0;

    const row_JBCL = (rowData, sectionID, rowID) => {
       if (index_JBCL > this.state.items_JBCL.length - 1) {
         index_JBCL = 0;
       }
       const obj = this.state.items_JBCL[index_JBCL++];
       let pathname_JBCL = `${pathPrefix}/eventContent/${obj._id}`;
       let data_JBCL = {
         id: obj._id,
         model: 'hall'
       }
       var path_JBCL = {
        pathname:pathname_JBCL,
        state:data_JBCL,
      }

       return (
         <Link to={path_JBCL}>
           <div className="hall-card" key={rowID}>
             <div className="hall-title">
               {obj.title}
             </div>
             <div className="hall-cover-wrapper" style={{background: `url(${obj.cover}) center no-repeat / cover`}}></div>
             <div className="hall-place">
               <div className="left-mark">展览地点</div>
               <div className="hall-place-content">{obj.address}</div>
             </div>
           </div>
         </Link>
       );
     };

     const row_CSZL = (rowData, sectionID, rowID) => {
       if (index_CSZL > this.state.items_CSZL.length - 1) {
         index_CSZL = 0;
       }
       const obj = this.state.items_CSZL[index_CSZL++];
       let pathname_CSZL = `${pathPrefix}/eventContent/${obj._id}`;
       let data_CSZL = {
         id: obj._id,
         model: 'hall'
       }
       var path_CSZL = {
        pathname:pathname_CSZL,
        state:data_CSZL,
      }

       return (
         <Link to={path_CSZL}>
            <div className="hall-card" key={rowID}>
              <div className="hall-title">
                {obj.title}
              </div>
              <div className="hall-cover-wrapper" style={{background: `url(${obj.cover}) center no-repeat / cover`}}></div>
              <div className="hall-place">
                <div className="left-mark">展览地点</div>
                <div className="hall-place-content">{obj.address}</div>
              </div>
            </div>
          </Link>
        );
      };

      return(
        <div className="hall-cell">
          <div className="hall-cell-list">
            <div className="hall_section">
              <div className="hall_section_square"></div>
              <div className="hall_section_title">基本陈列</div>
            </div>
            <ListView
                   ref={el => this.lv = el}
                   dataSource={this.state.dataSource_JBCL}
                   renderRow={row_JBCL}
                   className="hall-jbcl-list"
                   pageSize={5}
                   // useBodyScroll
                   style={{
                     height: '593.5px',
                     overflow: 'auto',
                   }}
                   onScroll={() => {  }}
                   scrollRenderAheadDistance={500}
                   onEndReachedThreshold={10}
                 />
           <div className="hall_section">
             <div className="hall_section_square"></div>
             <div className="hall_section_title">常设展览</div>
           </div>
           <ListView
                  ref={el => this.lv = el}
                  dataSource={this.state.dataSource_CSZL}
                  renderRow={row_CSZL}
                  className="hall-cszl-list"
                  pageSize={5}
                  // useBodyScroll
                  style={{
                    height: '593.5px',
                    overflow: 'auto',
                  }}
                  onScroll={() => {  }}
                  scrollRenderAheadDistance={500}
                  onEndReachedThreshold={10}
                />
          </div>
          <div className="hall-footer">
            <div className="bg-cc footer-image"></div>
          </div>
        </div>
      );
  }
}

export default HallModel;



