const getData = (args)=>{
    let data = new FormData();
    for(let i in args){
        data.append(i, args[i]);
    }
    return data;
}

export const fetchFile = (url, args) => {
    let data = getData(args);
    return fetch(url, {
        method: 'post',
        mode: 'cors',
        credentials: 'include',
        body: data
    })
    .then((response) => {
        if(response.status >= 200 && response.status < 300){
            return response.json();
        }else{
            let error = new Error(response.statusText);
            error.response = response;
            throw error;
        }
    })
    .then((data) => {
        return data;
    })
    .catch((error) => {
        Catch(error);
    });
}
export const fetchJson = (url, args)=>{
    return fetch(url, {
        method: 'post',
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
        },
        mode: 'cors',
        credentials: 'include',
        body: JSON.stringify(args)
    })
    .then((response) => {
        if(response.status >= 200 && response.status < 300){
            return response.json();
        }else{
            let error = new Error(response.statusText);
            error.response = response;
            throw error;
        }
    })
    .then((data) => {
        return data;
    })
    .catch((error) => {
        Catch(error);
    });
}



