import React, { Component } from 'react';
import './Exhibitions.css';
import { ListView } from 'antd-mobile';
import { Link } from 'react-router-dom';
import { Museum } from '../api'

const pathPrefix = window._config.pathPrefix;

class Exhibitions extends Component {

  constructor(props){
      super(props);
      const dataSource = new ListView.DataSource({
            rowHasChanged: (row1, row2) => row1 !== row2,
          });
      this.state = {
          items: [],
          dataSource,
          index: 0
        };
  }


  //耗时操作放在这里面
  async componentDidMount(){
    try{
      let data = await Museum.exhibitList({
        exhibition : this.props.location.state.id,
      });
      if(data && data.code===true){
        if (data.data.length > 0) {
          this.setState({
            dataSource: this.state.dataSource.cloneWithRows(data.data),
            items: data.data,
          })
        }
      }

    }catch(e){
      //
    }
  }

  clickHandler = (e) => {
  }


  render(){
    let exhibitpathname = `${pathPrefix}/eventContent/${this.props.location.state.id}`;

    var exhibitpath = {
     pathname:exhibitpathname,
     state:this.props.location.state,
    }

    let current_Data = this.state.items[this.state.index];
    let index = 0;
    const row = (rowData, sectionID, rowID) => {
        if (index > this.state.items.length - 1) {
          index = 0;
        }
        const obj = this.state.items[index++];
        return (
              <div  className="exhibitions-list-item" style={{background: `url(${obj.cover}) center no-repeat / cover`, marginRight:`${index === this.state.items.length ?  '15px' : '0px'}`}} onClick={()=>{this.setState({index: rowID})}}>

              </div>
        );

    };

    return(<div style={{width:"100vw", height:"100vh"}}>
              <div className="exhibitions-content">
                <Link to={exhibitpath}>
                  <div className="back-button">
                  </div>
                </Link>
                <div className="exhibitions-content-cover">
                  <img alt="" src={`${current_Data?current_Data.cover:''}`} style={{maxWidth:"100%"}} />
                </div>
                <div className="exhibitions-content-title">{current_Data?current_Data.title:""}</div>
                <div className="exhibitions-content-digest" dangerouslySetInnerHTML={{__html: current_Data?current_Data.content:''}} />
              </div>
              <div className="exhibitions-footerList">
                <div className="line"></div>
                <div className="footer-list">
                    <ListView
                           ref={el => this.lv = el}
                           dataSource={this.state.dataSource}
                           horizontal={true}
                           renderRow={row}
                           className="exhibitions-list"
                           pageSize={5}
                           useBodyScroll
                           onScroll={() => { }}
                           scrollRenderAheadDistance={500}
                           onEndReached={this.onEndReached}
                           onEndReachedThreshold={10}
                         />

                </div>
              </div>
           </div>);

  }
}
export default Exhibitions;



