import React, { Component } from 'react';
import './EventContent.css';
import { Link } from 'react-router-dom';
import { Museum } from '../api'
const pathPrefix = window._config.pathPrefix;

class EventContent extends Component {
    constructor(props){
        super(props);
        this.state = {
            result:{},
            hasExhibitions: false,
          };
    }

    timestampToTime =(timestamp) => {
          let date = new Date(timestamp);//时间戳为10位需*1000，时间戳为13位的话不需乘1000
          let Y = date.getFullYear() + '-';
          let M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
          let D = date.getDate();
          // let h = date.getHours() + ':';
          // let m = date.getMinutes() + ':';
          // let s = date.getSeconds();
          return Y+M+D;
    }


    async requestExhibitionData () {
      try{
        let data = await Museum.exhibitList({
          exhibition : this.props.location.state.id,
        });
        if(data && data.code===true){
          if (data.data.length > 0) {
            this.setState({
              hasExhibitions: true
            });
          }
        }
      }catch(e){
        //
      }
    }

    //耗时操作放在这里面
    async componentDidMount(){
      try{
        let data = await Museum.exhibitionContent({
          _id : this.props.location.state.id,
        });
        if (data && data.code === true) {
          this.setState({
            result: data.result,
          })
          this.requestExhibitionData();

        }

      }catch(e){
        //
      }
    }

     render(){
      let data = this.state.result || {};
      console.log(data);
      let pathname = `${pathPrefix}/exhibit/${data._id}/exhibitions`;
      let stateData = {
        id: data._id,
        model: this.props.location.state.model
      }
      var exhibitionsPath = {
       pathname:pathname,
       state:stateData,
     }
      return(
        <div style={{height:"100vh"}}>
          <div className="event-content-root">
            <div className="content-headcover" style={{background: `url(${data.cover}) center no-repeat / cover`}}>
              {this.state.hasExhibitions ? (<Link to={exhibitionsPath}>
                                              <div className="exhibition_button"></div>
                                            </Link>) : null}
              {data.virtual ? (<a href={data.virtual}><div className="virtual-exhibit"></div></a>) : null}
            </div>
            <div className="content-title">{data.title}</div>
            {(this.props.location.state.model === 'event') ?
              (<div className="info-event-bg">
                  <div className="info-bg">
                    <div className="info-top">
                      <div className="left-box">展览时间</div>
                      <div className="info-title">{this.timestampToTime(data.time ? data.time.start: "")}-{this.timestampToTime(data.time ? data.time.end: "")}</div>
                    </div>
                    <div className="info-bottom">
                        <div className="left-box">展览地点</div>
                        <div className="info-title">{data.address}</div>
                    </div>
                  </div>
               </div>) :
              (<div className="info-hall-bg">
                  <div className="hall-info-title">{data.address}</div>
               </div>)}
            <div className="line"></div>
            <div className="content" dangerouslySetInnerHTML={{__html: data.content}}/>
          </div>
          <div className="footer-image"></div>
        </div>

      );
     }
}

export default EventContent;



