import { fetchJson, fetchFile } from './FetchFun';
import { museum } from './Urls';

const ApiCreator = function(url, type=""){
    switch(type){
        case 'data':
            return function(args={}){
                return fetchJson(url, args);
            };
        case 'file':
            return function(args={}){
                return fetchFile(url, args);
            }
        default:
            return function(args={}){
                return fetchJson(url, args);
            };
    }
}
