import Home         from '../Home/Home.js'
import EventContent    from '../EventContent/EventContent.js'
import Exhibitions    from '../Exhibitions/Exhibitions.js'
const pathPrefix = window._config.pathPrefix;

const routes = [
    {
        path: pathPrefix + '/eventHome/:index?',
        component: Home,
    },
    {
        path: pathPrefix + '/eventContent/:id',
        component: EventContent,
    },
    {
      path: pathPrefix + '/exhibit/:id/exhibitions',
      component: Exhibitions,
    }

]
export default routes;



