import React, { Component } from 'react';
import './EventModel.css';
import { ListView } from 'antd-mobile';
import { Link } from 'react-router-dom';
import { Museum } from '../api'
const pathPrefix = window._config.pathPrefix;

const limit = 5;


class EventModel extends Component {

  constructor(props) {
    super(props);
    const dataSource = new ListView.DataSource({
      rowHasChanged: (row1, row2) => row1 !== row2,
    });

    this.state = {
        items: [],
        dataSource,
        isLoading: true,
        hasMore: true,
        page: 1,
        height: ''
    };
  }


  timestampToTime =(timestamp) => {
        let date = new Date(timestamp);//时间戳为10位需*1000，时间戳为13位的话不需乘1000
        let Y = date.getFullYear() + '-';
        let M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
        let D = date.getDate();
        // let h = date.getHours() + ':';
        // let m = date.getMinutes() + ':';
        // let s = date.getSeconds();
        return Y+M+D;
    }

    onEndReached = (event) => {
     // load new data
     // hasMore: from backend data, indicates whether it is the last page, here is false
     if (this.state.isLoading && !this.state.hasMore) {
       return;
     }
     if (this.state.hasMore) {
       this.setState({ isLoading: true });
       this.requestApplyData();
     }
   }

   async requestApplyData () {
     try{
       let data = await Museum.exhibitionList({
         limit : limit,
         page : this.state.page,
         type : "t",
         time : this.props.stateTime
       });
       if(data && data.code===true){
        let rData = [...this.state.items, ...data.data];
        this.setState({
          items: rData,
          dataSource: this.state.dataSource.cloneWithRows(rData),
          isLoading: false,
          hasMore: (data.data.length >= limit)? true : false,
          page: this.state.page + 1,
        });
       }
     }catch(e){
       //
     }
   }

  //耗时操作放在这里面
  async componentDidMount(){
    try{
      let data = await Museum.exhibitionList({
        limit : limit,
        page : this.state.page,
        type : "t",
        time : this.props.stateTime
      });

      if(data && data.code===true){
        this.setState({
          dataSource: this.state.dataSource.cloneWithRows(data.data),
          items: data.data,
          isLoading: false,
          hasMore: (data.data.length >= limit)? true : false,
          page: this.state.page + 1
        })
      }
    }catch(e){
      //
    }
  }

  render() {


    let index = this.state.items.length - 1;
    const row = (rowData, sectionID, rowID) => {
      if (index < 0) {
      index = this.state.items.length - 1;
      }
      const obj = this.state.items[index--];
       let pathname = `${pathPrefix}/eventContent/${obj._id}`;
       let data = {
         id: obj._id,
         model: 'event'
       }
       var path = {
        pathname:pathname,
        state:data,
      }

     return (
       <Link to={path}>
         <div className="event-card" key={rowID}>
           <div className="event-title">
             {obj.title}
           </div>
           <div className="event-cover-wrapper" style={{background: `url(${obj.cover}) center no-repeat / cover`}}></div>
           <div className="event-time">
             <div className="left-mark">展览时间</div>
             <div className="event-time-content">{this.timestampToTime(obj.time.start)}-{this.timestampToTime(obj.time.end)}</div>
           </div>
           <div className="event-place">
             <div className="left-mark">展览地点</div>
             <div className="event-place-content">{obj.address}</div>
           </div>
         </div>
       </Link>
     );
   };

    return(
      <div>
        <div className="event-cell">
          <ListView
                 ref={el => this.lv = el}
                 dataSource={this.state.dataSource}
                 renderHeader={() => (<div></div>)}
                 renderFooter={() => (
                 <div>
                   <div style={{ padding: 0, textAlign: 'center' }}>
                     {this.state.isLoading ? '加载中...' : (this.state.hasMore ? '加载更多' : '没有更多数据')}
                   </div>
                   <div className="event-footer">
                     <div className="bg-cc footer-image"></div>
                   </div>
                 </div>
                 )}
                 renderRow={row}
                 className="eventModel-list"
                 pageSize={5}
                 // useBodyScroll
                 style={{
                   height: 'calc(100vh - 45px)',
                   overflow: 'auto',
                 }}
                 onScroll={() => { }}
                 scrollRenderAheadDistance={500}
                 onEndReached={this.onEndReached}
                 onEndReachedThreshold={10}
               />
        </div>
      </div>



    );
  }
}

export default EventModel;



