import React, { Component } from 'react';
import './Home.css';
import { Tabs } from 'antd-mobile';
import EventModel from './EventModel.js'
import HallModel from './HallModel.js'
const apiPrefix = window._config.apiPrefix;

const tabs = [
  {title: "特展讯息"},
  {title: "固定陈列"},
  {title: "展览回顾"},
];

class Home extends Component {
  constructor(props) {
    super(props);
    let index = parseInt(this.props.match.params.index, 10);

      this.state = {
        index: index ? (index > 2 ? 2 : (index < 0 ? 0 : index)) : 0,
        hallURl: apiPrefix + '/exhibition/select?type=p',
        eventReviewURl: apiPrefix + '/exhibition/select?type=t&time=past',
        };
    }

    getInitialState = function () {
        return {
          checked: true
        };
      }

  render() {
    return (
      <div className="Home">
        <div style={{width:'100vw',height:'45px',top:'0',position:'fixed',zIndex:'2'}}>
          <Tabs
            tabs={tabs}
            initialPage={this.state.index}
            onChange={(tab, index) => {

            }}
            onTabClick={(tab, index) => {
              this.setState({
                index : index
              })
            }}
            swipeable={false}
          >
          </Tabs>
        </div>
        {(this.state.index ===  0) ? (<EventModel stateTime="noEnd"/>) : null}
        {(this.state.index ===  1) ? (<HallModel />) : null}
        {(this.state.index ===  2) ? (<EventModel stateTime="past"/>): null}
      </div>
    );
  }
}

export default Home;



